export default ".embla {\n  --slide-size: 70%;\n  --slide-spacing: 10px;\n  --slide-spacing-md-up: 20px;\n}\n\n.embla__container {\n  display: flex;\n  touch-action: pan-y pinch-zoom;\n  margin-left: calc(var(--slide-spacing) * -1); /* Renders -10px */\n}\n\n.embla__slide {\n  flex: 0 0 var(--slide-size);\n  min-width: 0;\n  padding-left: var(--slide-spacing);\n}\n\n@media (min-width: 768px) {\n  .embla__container {\n    margin-left: calc(var(--slide-spacing-md-up) * -1); /* Renders -20px */\n  }\n\n  .embla__slide {\n    padding-left: var(--slide-spacing-md-up);\n  }\n}\n";